import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const state = {
  cartList: []
}

const mutations = {
  addToCart(state, payload) {
    payload.count = 1;
    state.cartList.push(payload);
  },
  addCounter(state, index) {
    let arr = state.cartList[index];
    arr.count++;
    state.cartList.splice(index, 1, arr);
  },
  changeActive(state, iid) {
    state.cartList.forEach((item, index) => {
      if (item.iid === iid) {
        item.isActive = !item.isActive;
        state.cartList.splice(index, 1, item);
      }
    });
  },
  chgeAllActive(state, payload) {  // 激活所有
    state.cartList.forEach((item, index) => {
      if (!item.isActive) {
        item.isActive = !item.isActive;
        state.cartList.splice(index, 1, item);
      }
    });
  },
  unChgeAllActive(state, payload) {  // 全部不激活
    state.cartList.forEach((item, index) => {
      item.isActive = !item.isActive;
      state.cartList.splice(index, 1, item);
    });
  },
  deletCart(state, id) {
    let index;
    for (let i = 0; i < state.cartList.length; i++) {
      if (state.cartList[i].iid === id) {
        index = i; 
        break;
      }
    }
    state.cartList.splice(index, 1);
  }
}

const actions = {
  // 添加购物车
  addCart(context, payload) {
    return new Promise((resolve, reject) => {
      const len = context.state.cartList.length;
      let record = -1;
      for (let i = 0; i < len; i++) {
        if (context.state.cartList[i].iid === payload.iid) {
          context.commit('addCounter', i);
          record = len;
          resolve('添加成功');
          break;
        }
      }
      if (record !== len ) {
        context.commit('addToCart', payload);
        resolve('添加成功');
      }
    });
  }
}

const getters = {
  cartLength(state) {
    return state.cartList.length;
  },
  totalPrice(state) {
    return '￥' + state.cartList.filter(item => {
      return item.isActive;
    }).reduce((preValue, item) => {
      return preValue + (item.newPrice * item.count);
    }, 0)
  },
  getActiveCount(state) {
    return state.cartList.filter(item => {
      return item.isActive;
    }).length;
  }
}

const store = new Vuex.Store({
  state,
  mutations,
  actions,
  getters
});

export default store;