<template>
  <div class="tabbar-item" @click="itemClick">
    <div v-if="isActive"><slot v-if="isActive" name="item-icon"></slot></div>
    <div v-else><slot name="item-active-icon"></slot></div>
    <div :style="activeText"><slot name="item-text"></slot></div>
  </div>
</template>

<script>
export default {
  props: {
    path: {
      type: String,
      default: '/home'
    },
    activeColor: {
      type: String,
      default: 'red'
    }
  },
  data() {
    return {
      
    }
  },
  computed: {
    isActive() {
      return this.$route.path.indexOf(this.path);
    },
    activeText() {
      return !this.isActive ? {color: this.activeColor} : {};
    }
  },
  methods: {
    itemClick() {
      // 主页面路由功能
      this.$router.replace(this.path);
    }
  }
}
</script>

<style>
  .tabbar-item {
    flex: 1;
    color: #000;
    font-size: 14px;
    text-align: center;
  }
  .tabbar-item img {
    width: 24px;
    height: 24px;
    margin-top: 3px;
    margin-bottom: 2px;
    vertical-align: middle;
  }
  .active {
    color: #f00;
  }
</style>