<template>
  <div class="toast" v-show="isShow">
    {{ message }}
  </div>
</template>

<script>
export default {
  data () {
    return {
      message: '默认文字',
      isShow: false,
      bool: true
    }
  },
  methods: {
    show(mes, time) {
      if (this.bool) {
        this.bool = false;
        this.isShow = true;
        this.message = mes;
        setTimeout(() => {
          this.isShow = false;
          this.message = '';
          this.bool = true;
        }, time);
      }
    }
  }
}
</script>

<style scoped>
  .toast {
    position: fixed;
    top: 50%;
    left: 50%;
    font-size: 0.9rem;
    color: #f5f5f5;
    z-index: 9999;
    padding: 0.5rem 1.5rem;
    transform: translate(-50%);
    box-sizing: border-box;
    background-color: #141414;
  }
</style>
